import React, { Component } from 'react'
import {Modal, Button,Form,Alert} from 'react-bootstrap'
import './UserForm.css'
import Pic1 from './1.png'
import Pic2 from './2.png'
import Pic3 from './3.png'
import Pic4 from './4.png'
import Pic5 from './5.png'

class UserForm extends Component{
    state = {
        success: false
    }

    makeAlert() {
        console.log("called")
        this.setState({
            success: true
        })
        alert("Your Details Has been submitted and soon you will be alloted a shravan and will be emailed for the same.");
    }

    render(){
        return(
            <>
                <div className="text-center">
                    <img src={Pic1} className="img-fluid img-thumbnail" alt=""></img>
                    <img src={Pic2} className="img-fluid img-thumbnail" alt=""></img>
                    <img src={Pic3} className="img-fluid img-thumbnail" alt=""></img>
                    <img src={Pic4} className="img-fluid img-thumbnail" alt=""></img>
                    <img src={Pic5} className="img-fluid img-thumbnail" alt=""></img>
                </div>
                <div className="user-form" onSubmit={(e) => {this.makeAlert()}}>
                <h1>Fill this form and hire a shravan for your need</h1>
                <Form >
                {
                    (this.state.success)?(
                        <div className="alert alert-success" role="alert">
                            Your Details Has been submitted and soon you will be alloted a shravan and will be emailed for the same.
                        </div>
                    ):(<></>)
                    }
                    <Form.Group controlId="formBasicEmail">
                        <Form.Label>Person Name (Who is availaing the service)</Form.Label>
                        <Form.Control type="text" placeholder="Name" />
                        <Form.Text className="text-muted">
                        Tell us about yourself
                        </Form.Text>
                    </Form.Group>

                    <Form.Group controlId="formBasicEmail">
                        <Form.Label>Summary of the Person</Form.Label>
                        <Form.Control type="text" placeholder="Write about Yourself" />
                        <Form.Text className="text-muted">
                        Tell us about yourself
                        </Form.Text>
                    </Form.Group>

                    <Form.Group controlId="formBasicEmail">
                        <Form.Label>Your Relation with Person</Form.Label>
                        <Form.Control type="text" placeholder="Realtion" />
                    </Form.Group>
                    
                    <Form.Group controlId="formBasicCheckbox">
                        <Form.Label>Gender</Form.Label>
                        <Form.Check type="checkbox" label="Male" />
                        <Form.Check type="checkbox" label="Female" />
                    </Form.Group>

                    <Form.Group controlId="formBasicPassword">
                        <Form.Label>Address</Form.Label>
                        <Form.Control type="text" placeholder="Street with full adress" />
                    </Form.Group>

                    <Form.Group controlId="formBasicEmail">
                        <Form.Label>Qualities of Shravan Needed</Form.Label>
                        <Form.Control type="text" placeholder="Qualities with commas" />
                        <Form.Text className="text-muted">
                        What can you do for us?
                        </Form.Text>
                    </Form.Group>

                    <Button variant="primary" type="submit">
                        Submit
                    </Button>
                </Form>
                </div>
            </>
        )
    }
}

export default UserForm