import React, { Component } from 'react'
import {Modal, Button,Form,Alert} from 'react-bootstrap'
import './ShravanForm.css'
import Pic1 from './a.png'
import Pic2 from './b.png'
import Pic3 from './c.png'
import Pic4 from './d.png'
import Pic5 from './e.png'

class ShravanForm extends Component{

    state = {
        success: false
    }

    makeAlert() {
        console.log("called")
        this.setState({
            success: true
        })
        alert("Your Details Has been submitted");
    }

    render(){
        return(
            <>
                <div className="text-center">
                    <img src={Pic1} className="img-fluid img-thumbnail" alt=""></img>
                    <img src={Pic2} className="img-fluid img-thumbnail" alt=""></img>
                    <img src={Pic3} className="img-fluid img-thumbnail" alt=""></img>
                    <img src={Pic4} className="img-fluid img-thumbnail" alt=""></img>
                    <img src={Pic5} className="img-fluid img-thumbnail" alt=""></img>
                </div>
                <div className="shravan-form" onSubmit={(e)=> {this.makeAlert()}}>
                <h1>Fill this form and step towards becoming a shravan</h1>
                <Form>
                    {
                    (this.state.success)?(
                        <div className="alert alert-success" role="alert">
                            Details Submitted go back to Home
                        </div>
                    ):(<></>)
                    }
                    <Form.Group controlId="formBasicEmail">
                        <Form.Label>Summary</Form.Label>
                        <Form.Control type="text" placeholder="Write about Yourself" />
                        <Form.Text className="text-muted">
                        Tell us about yourself
                        </Form.Text>
                    </Form.Group>

                    
                    <Form.Group controlId="formBasicCheckbox">
                        <Form.Label>Gender</Form.Label>
                        <Form.Check type="checkbox" label="Male" />
                        <Form.Check type="checkbox" label="Female" />
                    </Form.Group>

                    <Form.Group controlId="formBasicPassword">
                        <Form.Label>Address</Form.Label>
                        <Form.Control type="text" placeholder="Street with full adress" />
                    </Form.Group>

                    <Form.Group controlId="formBasicEmail">
                        <Form.Label>Qualities</Form.Label>
                        <Form.Control type="text" placeholder="Qualities with commas" />
                        <Form.Text className="text-muted">
                        What can you do for us?
                        </Form.Text>
                    </Form.Group>

                    <Form.Group controlId="formBasicPassword">
                        <Form.Label>Email</Form.Label>
                        <Form.Control type="text" placeholder="Enter your email so that we can contact you" />
                    </Form.Group>

                    <Form.Group controlId="formBasicPassword">
                        <Form.Label>Phone No.</Form.Label>
                        <Form.Control type="text" placeholder="Enter your Phone number" />
                    </Form.Group>

                    <Button variant="primary" type="submit">
                        Submit
                    </Button>
                </Form>
                </div>
            </>
        )
    }
}

export default ShravanForm